        <b-button type="button" variant="secondary" class="rounded-circle dailer-pad-number" @click="sendDigit('4')">
          <div class="d-flex flex-column">
            <p class="mb-0 number">4</p>
            <p class="mb-0 small text">GHI</p>
          </div>
        </b-button>
        <b-button type="button" variant="secondary" class="rounded-circle dailer-pad-number" @click="sendDigit('5')">
