<script>
  export default {
    props: {
      connection: Object,
    },
    data() {
      return {
        phone_number: "",
      };
    },
    methods: {
      sendDigit(number) {
        this.phone_number = this.phone_number + number;
        this.$emit("digitPressed", number);
      },
      sendNumber() {
        this.$emit("phoneNumber", this.phone_number);
      },
    },
  };
</script>
